<template lang="en">
    <section>
        <loader :is-visible="isLoading"></loader>
        <div>
            <div class="row">
                <div class="col-md-12 padding-t_8">
                    <div class="theme-primary partition-full">
                        <span class="p-name text-white">{{$t('AddApprovalChain')}}</span>
                    </div>
                </div>
            </div>
        </div>
        <div class="border p-3">
            <dynamic-form lang="en" :buttons="buttons" :schema="FormSchema" v-on:OnSubmit="onSubmit"></dynamic-form>
            <small class="text-danger">Fields marked with an asterisk (*) are mandatory.</small>
        </div>
    </section>
    </template>
    
    <script>
    import DataService from '../../service/DataService';
    export default {
    
        data() {
    
            return {
                isLoading: false,
                DataLoaded: 0,
                ApprovalChainId: 0,
                groupList: [],
                TagsSelectedArray: [],
                FormSchema: [{
                    layoutType: "triple",
                    Data: [{
                        astype: "TextField",
                        label: this.$t('ApprovalChainName'),
                        name: "ApprovalChainName",
                        value: "",
                        placeholder: "",
                        validationRules: {
                            "required": true
                        },
                    },
                    {
                        astype: "SelectField",
                        label: this.$t('Status'),
                        name: "Status",
                        value: 1001,
                        placeholder: "",
                        validationRules: {
                            "required": true
                        },
                        config: {
                            options: [{
                                value: "1001",
                                name: 'Active'
                            }, {
                                value: "1002",
                                name: 'Inactive'
                            }]
                        },
                    },
                    {
                        astype: "MultiSelectField",
                        label: this.$t('SelectApprovalGroup'),
                        name: "ApprovalGroup",
                        value: "",
                        mode: "tag",
                        placeholder: "",
                        validationRules: {
                            "required": true
                        },
                        config: {
                            options: []
                        },
                    },
    
                    ]
                },
                {
                    layoutType: "Single",
                    Data: [{
                        astype: "TextAreaField",
                        label: this.$t('Description'),
                        name: "Description",
                        value: "",
                        placeholder: "",
                        validationRules: {
                            "required": false
                        },
                    },]
                },
                ],
                buttons: [{
                    type: "submit",
                    class: "btn btn-success",
                    text: "<i class='fa fa-save pr-2'></i>Save",
                },
    
                {
                    type: "button",
                    class: "btn btn-danger",
                    text: "<i class='fa fa-close pr-2'></i>Cancel",
                    onClick: this.onCancel
                }
                ],
            }
        },
        created: async function () {
            await this.GetApprovalGroups();
            if (this.$route.params.id != null) {
                this.ApprovalChainId = this.$route.params.id;
                await this.GetApprovalGroups();
                await this.ManageChain();
            }
            this.DataLoaded = 1;
        },
        methods: {
            onSubmit: function (formfields) {
                var vm = this;
                var jsonobj = {
                    ApprovalChainId: vm.ApprovalChainId,
                    ApprovalChainName: formfields.ApprovalChainName,
                    StatusId: formfields.Status,
                    Groups: Array.prototype.map.call(formfields.ApprovalGroup, function (item) {
                        return item.value;
                    }).join(","),
                    Description: formfields.Description,
                }
                var poststring = JSON.stringify(jsonobj);
                DataService.AddApprovalChain(poststring).then(response => {
                    if (response.data == '-1') {
                        vm.ShowAlert(vm.$t('AlreadyExists'), "warning", true, vm.$t("Alert"));
                        vm.onCancel();
                    } else if (response.data == '-2') {
                        vm.ShowAlert(vm.$t('AlreadyExists'), "warning", true, vm.$t("Alert"));
                        vm.onCancel();
                    } else if (response.data == '-3') {
                        vm.ShowAlert(vm.$t("Something went Wrong"), "failure", true, vm.$t('Alert'));
                        vm.onCancel();
                    } else {
                        vm.ShowAlert(vm.$t("ApprovalChainSuccessfullyAdded"), "Success", true, vm.$t('Alert'));
                        vm.onCancel();
                    }
                });
            },
    
            GetApprovalGroups: async function () {
                var vm = this;
                DataService.GetApprovalGroupByCompanyId().then(response => {
                    var json = response.data;
                    if (json != null) {
                        json.forEach(function (item, index) {
                            (vm.FormSchema)[0].Data[2].config.options.push({
                                name: item.APPROVAL_GROUP_NAME,
                                value: `${item.APPROVAL_GROUP_ID}`
                            })
    
                        });
                        return vm.LeadStatusList;
                    }
                });
            },
            ManageChain: async function () {
                var vm = this;
                var url = `id=${this.$route.params.id}`
                await DataService.GetByChainId(url).then(response => {
                    var chainData = JSON.parse(response.data)
                    if (chainData.length > 0) {
                        (this.FormSchema)[0].Data[0].value = chainData[0].approval_chain_name;
                        (this.FormSchema)[0].Data[1].value = chainData[0].status_id;
                        vm.groupList = chainData[0].GROUPS.split(",");
                        var UserList = [];
                        for (let i = 0; i < vm.groupList.length - 1; i++) {
                            UserList.push(vm.groupList[i]);
                        }
                        UserList.forEach((item) => {
                            debugger
                            var obj = (vm.FormSchema)[0].Data[2].config.options.find(x => x.value == item);
                            if (obj != undefined) {
                                vm.TagsSelectedArray.push({
                                    label: obj.name,
                                    value: obj.value
                                })
                            }
                        });
                        (this.FormSchema)[0].Data[2].value = vm.TagsSelectedArray;
                        (this.FormSchema)[1].Data[0].value = chainData[0].description;
                    }
                })
            },
            onCancel: function () {
                this.$router.push({
                    name: 'Approval Chain'
                });
            },
        }
    }
    </script>
    